___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
}

.navbar {
  box-shadow: 4px 4px 0 rgba(31, 30, 30, 0.274);
}

.navbar,
footer {
  background-color: blueviolet;
}

.navbar,
.nav-items {
  display: flex;
}

.nav-items {
  margin-left: auto;
}

.nav-items li {
  margin: 2rem;
  font-size: 20px;
  color: #fff;
  cursor: pointer;
}

ul {
  list-style: none;
}

.logo {
  width: 5rem;
  height: 5rem;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  background-repeat: no-repeat;
  margin: 1rem;
  border: 2px solid #fff;
  -moz-box-shadow: 0 6px 5px #ccc;
  -webkit-box-shadow: 0 6px 5px #ccc;
  box-shadow: 0 6px 5px #ccc;
  -moz-border-radius: 190px;
  -webkit-border-radius: 190px;
  border-radius: 50%;
}

.show {
  width: 13rem;
  border: 1px solid #000;
  border-radius: 5%;
  margin: 1rem;
  background-color: rgb(61, 59, 57);
  box-shadow: 0.1px 0.1px 3px 5px rgba(34, 2, 2, 0.7);
}

.logo:hover,
.show:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

.logo:hover {
  transform: scale(1.5);
}

.show:hover {
  transform: scale(1.1);
}

footer {
  padding: 1rem;
  color: #fff;
}

.select {
  display: flex;
  flex-direction: row;
  list-style: none;
  text-align: center;
  justify-content: space-around;
}

.select li {
  cursor: pointer;
}

.select li:active {
  text-decoration: underline;
}

.active {
  text-decoration: underline;
  color: red !important;
}

.movie-list {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
}

#latest-items {
  display: flex;
  flex-direction: row-reverse;
}

.show img {
  width: 100%;
  border-radius: 5%;
}

.show h3 {
  width: 100%;
  text-align: center;
}

.fas {
  color: #fff;
  cursor: pointer;
  justify-content: space-between;
  padding: 0.5rem;
}

.interaction-icons {
  display: flex;
  justify-content: space-between;
}

.like {
  display: flex;
  align-self: center !important;
}

.like-display {
  align-self: center !important;
}

div #comment-display {
  border: 1px solid red;
}

.close-button {
  color: black;
}

.comment-popup {
  top: 2rem;
  left: 2rem;
  right: 2rem;
  bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: #fff;
  border-radius: 2rem;
  padding: 5% 4% 2rem;
  z-index: 9999;
  width: 80%;
  height: 80%;
  overflow: auto;
  position: fixed;
  color: #000;
}

.comment-popup img {
  width: max-content;
  height: 100%;
}

.detail-view {
  display: flex;
  gap: 20px;
}

#comment-count #comment-display {
  margin: 0 auto;
}

.comment-area {
  flex-direction: column;
  justify-self: center;
}
